* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
p {
  margin: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list__item {
  display: flex;
  align-items: center;
}

.country-name {
  font-size: 40px;
  margin-left: 10px;
  font-weight: 700;
}

.country-info-top {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.country-title {
  color: green;
  font-size: 50px;
  font-weight: 700;
  margin-left: 10px;
}

.country-info-text {
  font-size: 20px;
}

.country-info-text:not(:last-child) {
  margin-bottom: 10px;
}
